// API åŸºç¡€é…ç½®
const API_BASE_URL = import.meta.env.VITE_API_BASE_URL || '/api';

// HTTP è¯·æ±‚å°è£…
class API {
    // é€šç”¨ JSON è¯·æ±‚
    static async request(endpoint, options = {}) {
        const url = `${API_BASE_URL}${endpoint}`;
        const config = {
            headers: {
                'Content-Type': 'application/json',
                ...options.headers,
            },
            ...options,
        };

        try {
            const response = await fetch(url, config);
            const contentType = response.headers.get('content-type') || '';

            let data;
            if (contentType.includes('application/json')) {
                data = await response.json();
            } else {
                // åç«¯è¿”å›çº¯å­—ç¬¦ä¸²çš„æƒ…å†µ
                data = await response.text();
            }

            if (!response.ok) {
                const msg = (typeof data === 'object' && data.message) ? data.message : data;
                throw new Error(msg || `è¯·æ±‚å¤±è´¥ (${response.status})`);
            }

            return data;
        } catch (error) {
            console.error(`API Error [${endpoint}]:`, error);
            throw error;
        }
    }

    // GET è¯·æ±‚ï¼šå‚æ•°æ‹¼åˆ° URL
    static get(endpoint, params) {
        const query = params ? '?' + new URLSearchParams(params).toString() : '';
        return this.request(endpoint + query, { method: 'GET' });
    }

    // POST è¯·æ±‚ï¼šJSON Body
    static post(endpoint, body) {
        return this.request(endpoint, {
            method: 'POST',
            body: body ? JSON.stringify(body) : undefined,
        });
    }

    // POST è¯·æ±‚ï¼šURL æŸ¥è¯¢å‚æ•° + JSON Body (åç«¯å¾ˆå¤šæ¥å£æ˜¯ @RequestParam + @RequestBody æ··åˆ)
    static postWithParams(endpoint, params, body) {
        const query = params ? '?' + new URLSearchParams(params).toString() : '';
        return this.request(endpoint + query, {
            method: 'POST',
            body: body ? JSON.stringify(body) : undefined,
        });
    }
}

// ============ è®¤è¯ API ============
export const authAPI = {
    // æ³¨å†Œï¼š@RequestBody RegisterReq {username, password, nickname, initialCiv}
    register(username, password, nickname, initialCiv) {
        return API.post('/auth/register', { username, password, nickname, initialCiv });
    },

    // ç™»å½•ï¼š@RequestBody LoginReq {username, password}  â†’ è¿”å›çº¯å­—ç¬¦ä¸²
    login(username, password) {
        return API.post('/auth/login', { username, password });
    },
};

